<template>
    <div>
        <div>
            <el-row>
                <el-button v-for="item in wetGarbage" :key="item" type="success" plain @click="handleDialog(1)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in recyclableTrash" :key="item" type="primary" plain @click="handleDialog(2)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in HarmfulGarbage" :key="item" type="danger" plain @click="handleDialog(3)">
                    {{item}}
                </el-button>
            </el-row>
        </div>
        <div class="search-btn-next">
            <el-row>
                <el-button v-for="item in DryGarbage" :key="item" type="info" plain @click="handleDialog(4)">{{item}}
                </el-button>
            </el-row>
        </div>
        <el-form :inline="true" :model="query" class="query-form" size="mini">
            <el-form-item class="query-form-item">
                <el-input v-model="query.keywords" placeholder="直接搜索垃圾名称"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" icon="search" @click="onSubmit">查询</el-button>
                </el-button-group>
            </el-form-item>
        </el-form>
    </div>

</template>

<script>
    import { search } from "../../api/garbage/garbage";

    export default {
        name: 'garbage_index',
        data() {
            return {
                query: {
                    keywords: '' // 搜索关键词
                },
                loading: true,
                result: {
                    cat_title: '', //标题
                    cat_description: '' // 描述
                }, // 搜索接口数据
                title: '',
                description: '',
                wetGarbage: ['食品类', '水果类', '盆景残渣类', '果壳类'], // 湿垃圾，厨余垃圾
                recyclableTrash: ['塑料橡胶类', '金属类', '玻璃类', '纸张类'], // 可回收垃圾
                HarmfulGarbage: ['电池电子类', '灯管类', '油漆溶剂类', '过期药品类'], // 有害垃圾
                DryGarbage: ['纸巾类', '一次性用具', '灰尘渣土', '砖瓦陶瓷'] // 干垃圾，其他垃圾
            };
        },
        components: {},
        methods: {
            handleDialog(val) {
                switch (val) {
                    case 1:
                        this.title = '厨余垃圾'
                        this.description = '俗称“湿垃圾”，包括居民家庭产生的剩饭剩菜、菜根菜叶、瓜果皮核渣、动物内脏、过期食品等食品类废物及农贸市场的有机垃圾。'
                        break;
                    case 2:
                        this.title = '可回收物'
                        this.description = '是指经过加工可以成为生产原料或者经过整理可以再利用的物品，主要包括废纸、塑料、金属、玻璃等。可回收物在投放前需要保持清洁干燥。'
                        break;
                    case 3:
                        this.title = '有害垃圾'
                        this.description = '是指垃圾中对人体健康或自然环境造成直接或潜在危害的物质。包括废充电电池、废荧光灯管、废水银温度计、废油漆桶、过期药品等日常用品，这些垃圾需要特殊安全处理。'
                        break;
                    case 4:
                        this.title = '其他垃圾'
                        this.description = '俗称“干垃圾”。包括除上述几类垃圾之外的砖瓦陶瓷、尘土、卫生间废纸、纸巾等难以回收以及暂无回收利用价值的废弃物。'
                        break;
                }
                this.open()
            },
            // 弹框
            open() {
                this.$alert(this.description, this.title, {
                    confirmButtonText: '确定',
                    /*callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }*/
                });
            },
            // 搜索
            onSubmit() {
                this.loading = true;
                search(this.query)
                    .then(response => {
                        this.result = response
                        if (this.result.success === 1) {
                            this.loading = false;
                            this.title = this.result.cat_title
                            this.description = this.result.cat_description
                            this.open()
                        } else {
                            this.$message.error(response.msg);
                        }

                    })
                    .catch(() => {
                        this.loading = false;
                        this.result = {};
                    });
            },
        },
        filters: {},
        mounted() {
            document.body.ondrop = function (event) {
                event.preventDefault();
                event.stopPropagation();
            };
        },
        created() {

        }
    };
</script>

<style type="text/scss" lang="scss" scoped>
    .search-btn-next {
        margin-top: 10px;
    }
</style>
