import axios from "../../utils/axios";

// 列表
export function list(query) {
    return axios({
        url: "/weibo/list",
        method: "get",
        params: query
    });
}

// 最新微博列表
export function home(query) {
    return axios({
        url: "/weibo/home",
        method: "get",
        params: query
    });
}

// 热搜词列表
export function hot() {
    return axios({
        url: '/weibo/hot',
        method: 'get'
    })
}

// 热搜榜
export function hotSearch() {
    return axios({
        url: '/weibo/hotSearch',
        method: 'get'
    })
}

// 热搜词详情
export function hotDesc(desc) {
    return axios({
        url: '/weibo/hotDesc',
        method: 'get',
        params: desc
    })
}
