<template>
    <div>
        <el-form :inline="true" :model="query" class="query-form" size="mini">
            <el-form-item class="query-form-item">
                <el-input :placeholder="hotwords" disabled style="width: 330px"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button-group>
                    <el-button type="primary" icon="el-icon-refresh" @click="getList">刷新</el-button>
                </el-button-group>
            </el-form-item>
        </el-form>
        <el-link type="primary" class="hot-search-link" v-for="(item,index) in hot_search" :key="index"
                 @click="item.title_sub === '微博热搜榜'?moreHot():hotDesc(item.title_sub)">
            {{item.title_sub === '微博热搜榜'?'更多热搜':item.title_sub}}
            <el-image v-if="item.icon && item.title_sub !== '微博热搜榜'" :src="item.icon" class="hot-search-img"></el-image>
        </el-link>


        <el-card v-if="this.morehot">{{'实时热点，每分钟更新一次'}}</el-card>
        <el-table :data="hotSearchData" style="width: 100%" height="500" v-if="this.morehot">
            <el-table-column label="榜单" width="100">
                <template slot-scope="scope">
                    <el-image v-if="scope.row.pic" :src="scope.row.pic" class="pic"></el-image>
                </template>
            </el-table-column>
            <el-table-column label="热搜词" width="300">
                <template slot-scope="scope">
                    {{scope.row.desc}}
                    <el-image
                        v-if="scope.row.icon" :src="scope.row.icon"
                        class="icon"/>
                </template>
            </el-table-column>
            <el-table-column label="热搜指数" width="120">
                <template slot-scope="scope">
                    {{scope.row.desc_extr || ''}}
                </template>
            </el-table-column>
        </el-table>

        <el-card class="box-card" v-if="this.cardlist_title">
            <div class="clearfix">
                <el-image v-if="portrait" :src="portrait" class="icon"></el-image>
                <br>
                <span>{{'#'+cardlist_title+'#'}}</span>
            </div>
            <div class="text item" v-if="this.lead">
                {{lead}}
            </div>
        </el-card>
        <el-table :data="complexMblog" height="500" v-if="this.complexMblog" v-loading="loading">
            <el-table-column label="内容" width="400">
                <template slot-scope="scope">
                    <b v-html="scope.row.text"></b>
                </template>
            </el-table-column>
            <el-table-column label="图片/视频" width="350">
                <template slot-scope="scope">
                    <viewer v-if="scope.row.pics">
                        <el-carousel :interval="4000" type="card" height="200px">
                            <el-carousel-item v-for="(pic,index) in scope.row.pics" :key="index">
                                <img :src="pic.large.url" v-if="pic.large.url">
                            </el-carousel-item>
                        </el-carousel>
                    </viewer>
                    <video v-if="scope.row.page_info && scope.row.page_info.media_info"
                           :src="scope.row.page_info.media_info.mp4_sd_url" controls="controls"
                           style="width: 100%;"></video>
                </template>
            </el-table-column>
            <el-table-column label="发布账号" width="150">
                <template slot-scope="scope">
                    {{scope.row.user.screen_name}}
                </template>
            </el-table-column>
            <el-table-column label="发布时间" width="120">
                <template slot-scope="scope">
                    {{scope.row.created_at || ''}}
                </template>
            </el-table-column>
            <el-table-column label="发布客户端" width="120" prop="source"></el-table-column>
        </el-table>
        <div class="block" v-if="complexMblog">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                background
                layout="prev, pager, next"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {
        hot, // 热搜词列表
        hotSearch, // 更多热搜
        hotDesc, // 热搜词详细
    } from "../../api/wb/wb";

    export default {
        data() {
            return {
                hotwords: '', // 热门搜索提示
                hot_search: [], // 热门搜索关键词列表
                query: {
                    page: 1,
                    limit: 10
                },
                currentPage: 1, // 当前页
                total: 0,
                loading: true,
                list: [],
                morehot: false, // 是否显示更多热搜
                hotSearchData: [
                    {
                        desc: '', // 热搜词
                        icon: '', // 热搜词后跟的图标
                        desc_extr: '', // 热搜指数
                    }
                ], // 热搜榜
                cardlist_title: '', // 热词名称
                complexHot: [], // 综合热门
                lead: '', // 热词导语
                portrait: '', // 肖像or热词图标
                complexMblog: '', // 综合微博
            };
        },
        components: {},
        methods: {
            // 参数初始化
            init() {
                this.morehot = false;
                this.hotSearchData = [];
                this.cardlist_title = '';
                this.complexHot = [];
                this.lead = '';
                this.portrait = '';
                this.complexMblog = '';
            },

            // 分页
            handleCurrentChange(val) {
                this.query.page = val;
                this.currentPage = val;
                this.hotDesc(this.cardlist_title, this.query.page);
            },
            // 热搜词
            getList(name) {
                this.init() // 参数初始化
                hot().then(response => {
                    console.log(response)
                    const data = response.data || {}
                    if (response.ok === 1) {
                        this.hotwords = data.hotwords[0]['tip'] + data.hotwords[0]['word'] // 搜索框提示词
                        this.hot_search = data.cards[0]['card_group'][0]['group'] // 热门搜索词
                    }
                    this.loading = false;
                }).catch(() => {
                    this.loading = true;
                });
            },

            // 更多热搜
            moreHot() {
                this.init()
                this.morehot = true // 显示更多热搜榜
                this.hotSearch()
            },
            // 热搜榜
            hotSearch() {
                hotSearch().then(response => {
                    // console.log(response)
                    const data = response.data || {}
                    if (response.ok === 1) {
                        this.hotSearchData = data.cards[0]['card_group']
                    }
                }).catch(() => {
                    this.loading = false;
                });
            },
            //热搜词详细
            hotDesc(desc, page) {
                this.init()
                let params = {
                    desc: desc, // 热词
                    page: page  // 分页
                }
                hotDesc(params).then(response => {
                    // console.log(response)
                    const data = response.data || {}
                    if (response.ok === 1) {
                        this.cardlist_title = desc
                        this.lead = data.cards[0]['desc'] // 导语
                        this.portrait = data.cardlistInfo.portrait // 热词图标
                        this.total = data.cardlistInfo.total // 总页码数
                        let mblogArray = []
                        if (page > 1) {
                            let complexHot = this.complexHot = data.cards[0]['card_group'] // 分页的综合热门
                            complexHot.map(item => {
                                mblogArray.push(item.mblog)
                            })
                        } else {
                            this.currentPage = 1 // 重新刷新分页值
                            let complexHot = this.complexHot = data.cards.filter((elem, index) => index !== 0) // 综合热门
                            complexHot.map(item => {
                                item.card_group.map(mblog => {
                                    mblogArray.push(mblog.mblog)
                                })
                            })
                        }
                        this.complexMblog = mblogArray.filter(function (e) {
                            return e
                        }) // 过滤空数据
                    } else {
                        this.$message.error('微博抽风了');
                    }
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
        filters: {},
        mounted() {
            document.body.ondrop = function (event) {
                event.preventDefault();
                event.stopPropagation();
            };
        },
        created() {
            this.getList();
        }
    };
</script>

<style type="text/scss" lang="scss" scoped>
    .hot-search-link {
        margin-left: 10px;
    }

    .hot-search-img {
        height: 10%;
        width: 10%;
        vertical-align: top;
    }

    .pic {
        width: 50%;
        height: 50%
    }

    .icon {
        width: 10%;
        height: 10%;
        vertical-align: top;
    }
</style>
